<template>
  <div class="text-reader">
    <p class="text-reader-text">{{ text }}</p>
    <textarea @click="wordIndexChange"  @input="changeActiveText" name="" id="" class="text-reader-input" placeholder="Введите ваш текст" v-model="currentText"></textarea>
    <div class="text-reader-button">
      <div class="text-r-button-top">
        <div class="t-r-button-top-l">
          <button v-if="!isStarted" class="go" @click="start()"> начало</button>
          <button v-if="isStarted" class="go" @click="stop()"> стоп</button>
          <button @click="clearText()" class="clear">Очистить</button>
        </div>
        <button @click="changeIsSettingsOpen()" class="advanced">настройки</button>
      </div>
      <div class="t-menu" :class="{active : isSettingsOpen}">
        <div class="t-menu-item">
          <label for="" class="speedInputLabel">
            <p v-if="!isNormalSpeed" class="error">введите скорость больше нуля</p>
            <p v-if="isNormalSpeed" class="noterror">скорость слов/минута</p>
            <input type="number" class="speed" placeholder="слов в минуту" @input="changeSpeed" value="300">
          </label>
          <div class="speedInfo">времени на чтение: {{ timeForRead }} секунд</div>
        </div>
        <div class="t-menu-item">
          <label class="t-menu-is-clear">
            <input @change="changeEndClear()" type="checkbox" class="t-menu-is-clear-checkbox">
            <p class="t-menu-is-clear-text">Очищать после прочтения</p>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
      document.addEventListener('keydown', this.keyDown); // здесь я его регистрирую
  },
  data(){
    return{
      text: 'Ожидание текста',
      currentText: '',
      currentTextNotReady: '',
      textArr: [],
      wordIndex: 0,
      speed: 200,
      timeForRead: 0,
      speedInfo: 300,
      isNormalSpeed: true,
      isStarted: false,
      intervalId: '',
      isEndCllear: false,
      isSettingsOpen: false,
    }
  },

  methods: {
    changeActiveText(e){
      this.currentTextNotReady = e.target.value.replace(/\n/g, ' ');
      this.currentText = this.currentTextNotReady;
      this.textArr = this.currentText.split(' ')
      this.text = this.currentText.split(' ')[0];
      for(let i = 0; this.textArr.length > i; i++) {
        if(this.textArr[i] === '−' || this.textArr[i] === '–' || this.textArr[i] === '-' ) {
          this.textArr.splice(i, 1)
        }
      }
      this.timeForRead =  Math.round((this.speed * this.textArr.length) / 1000);
    },
    keyDown(e){
      if(e.key === "Backspace"){
        if(this.text == '') {
          this.text = 'Ожидание текста'
        }
      } else {
        return 0;
      }
    },
    start(){
      if(this.currentText != '') {
        this.isStarted = true;
        this.intervalId = setInterval(() => {
            if (this.wordIndex < this.textArr.length) {
                this.text = this.textArr[this.wordIndex]
                this.wordIndex++;
            } else {
                clearInterval(this.intervalId);
                this.isStarted = false;
                this.wordIndex = 0;
                  if(this.isEndCllear) {
                    setTimeout(() => {
                      this.currentText = ''
                      this.currentTextNotReady = ''
                      this.text = 'Ожидание текста'
                    }, 500);
                  }
            }
        }, this.speed);
      }
    },
    stop(){
      this.isStarted = false
      clearInterval(this.intervalId)
    },
    changeText(text){
      this.text = text;
    },
    changeSpeed(e){
      if(e.target.value > 0) {
        this.isNormalSpeed = true;
        this.speedInfo = e.target.value;
        this.speed =  Math.round(60000 / this.speedInfo) 
      } else {
        this.isNormalSpeed = false;
      }
      this.timeForRead =  Math.round((this.speed * this.textArr.length) / 60);
    }, 
    wordIndexChange(el) {
      let index = el.target.selectionStart;
      let word = '';
      let text = el.target.value;
      if(text[index - 1] != ' ' && text[index - 1] != undefined) {
        while(text[index - 1] != ' ' && text[index - 1] != undefined) {
          index --
          
        }
        
      }
      for(let i = index; text.length > i; i++) {
        if(text[index] != ' ') {
          word = word + text[index]
          index ++
        } else if (text[index - 1] == ' ' ||  text[index + 1] == undefined ) {
          return word
        }
      }
      for(let i = 0; this.textArr.length > i; i++) {
        if(this.textArr[i] == word) {
          this.wordIndex= i;
        }
      }

      if(word == '') {
        word = 'Ожидание текста'
      }

      this.text = word

    },
    changeEndClear(){
      this.isEndCllear = !this.isEndCllear;
    },
    clearText() {
      this.currentText = ''
      this.currentTextNotReady = ''
      this.text = 'Ожидание текста'
    },
    changeIsSettingsOpen() {
      this.isSettingsOpen = !this.isSettingsOpen
    },

  },
}

</script>

